
import { mapGetters, mapMutations } from 'vuex';
export default {
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  data() {
    return {
      pageSize: 6,
    };
  },
  methods:{
      ...mapMutations(['changePage'])
  },
  computed:{
      ...mapGetters(['pageNumber', 'currentPage']),
  }
};
